.first {
  &__rounds {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 20%;
    background-image: url(/img/elips.png);
    -webkit-background-size: cover;
    background-position: top 0; }
  &__video {
    opacity: .05;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0; }
  &__subtitle {
    @include em(18);
    color: #c3c3c3;
    font-weight: 500; }
  &__text {
    @include em(14);
    color: #c3c3c3; } }
