.page {
  display: flex;
  flex-direction: column;
  height: 100%;
  &__content {
    flex: 1 0 auto; }
  &__footer {
    flex: 0 0 auto; } }

.section {
  &_dark {
    background-color: #1c1c1e; }
  &_full {
    min-height: 100vh; }
  &_first {
    position: relative;
    .container {
      min-height: 100vh;
      position: relative;
      z-index: 3; } } }

.modal-open {
  cursor: pointer; }

.title {
  font-family: "Raleway", sans-serif;
  &_main {
    @include em(52);
    font-weight: 800;
    line-height: 1.3;
    margin: 0; }
  &_white {
    color: #fff; } }
